.partners {
  background-image: url("../../assets/images/partners-page/bg.png");
  width: 100%;
  height: 110vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .animation-partner-img-group-up {
    animation-name: animation-partner-img-group-up !important;
    animation-duration: 0.5s !important;
    animation-fill-mode: both !important;
  }

  .partner-main {
    position: relative;
    display: flex;
    align-items: center;
    animation-name: animation-partner;
    animation-duration: 1s;
    animation-fill-mode: both;

    .partner-main-bg {
      height: 100vh;
      margin: 0 auto;
    }

    .partner-img-group {
      position: absolute;
      display: flex;
      top: 100px;
      justify-content: center;
      flex-wrap: wrap;
      animation-duration: 2s;
      animation-fill-mode: backwards;
      animation-name: animation-partner-img-group;
      animation-duration: 1s;
      animation-fill-mode: both;

      img {
        padding: 80px;
      }
    }
  }
}

@keyframes animation-partner-main {
  0% {
    opacity: 0;
    margin-top: 30px;
  }

  100% {
    opacity: 1;
    margin-top: 0px;
  }
}

@keyframes animation-partner-group {
  0% {
    top: 100px;
  }

  100% {
    top: -1000px;
  }
}

@keyframes animation-partner {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes animation-partner-img-group {
  0% {
    opacity: 0;
    margin-top: 200px;
  }

  100% {
    opacity: 1;
    margin-top: 0px;
  }
}

@keyframes animation-partner-img-group-up {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    margin-top: -200px;
  }
}
